/* Portfolio Page Specific Styles */

div#portfolio {
  overflow: hidden;
}

.portfolio-nav {
  position: absolute;
  font-size: 30px;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
  z-index: 10;
  color: #1084d0;
  transition: color 0.2s, opacity 0.2s;
}

.portfolio-nav:hover {
  color: #0066cc;
  opacity: 0.8;
}

.portfolio-nav.left {
  left: 30px;
}

.portfolio-nav.right {
  right: 30px;
}

.projects {
  margin: 40px 0;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  min-height: 400px;
  position: relative;
  align-items: center;
  justify-content: center;
}

.project {
  width: 100%;
  min-height: 400px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

/* Fixed size image container - base styles (themes override appearance) */
.project-image {
  width: 200px;
  height: 200px;
  max-width: 90%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px;
  position: relative;
  overflow: hidden;
  transition: transform 0.2s, box-shadow 0.2s;
}

.project-image a {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.project-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  background: #ffffff;
}

/* Project info section */
.project-info {
  padding: 10px;
  max-width: 600px;
}

.project-info h2 {
  margin: 10px 0;
  color: #2b2be7;
  font-size: 16px;
}

.project-info p {
  margin: 12px 0;
  line-height: 1.6;
}

/* Project counter */
.project-counter {
  margin-top: 16px;
  padding: 8px;
  background: linear-gradient(90deg, #e8f0ff 0%, #f0f8ff 50%, #e8f0ff 100%);
  border: 1px solid #c0c0c0;
  display: inline-block;
  font-size: 10px;
  color: #000080;
  font-weight: bold;
  letter-spacing: 1px;
}

/* Loading state */
.project .spinner {
  color: #1084d0;
  font-size: 32px;
}

/* Portfolio-specific badge styles */
.browser-viewport .retro-badge {
  display: inline-block;
  padding: 4px 8px;
  margin: 0 6px;
  font-size: 10px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 1px;
  border: 1px solid;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
}

.browser-viewport .retro-badge.hot {
  background: linear-gradient(135deg, #ff6666 0%, #ff0000 100%);
  color: #ffffff;
  border-color: #cc0000;
  animation: pulse-badge 1.5s ease-in-out infinite;
}

.browser-viewport .retro-badge.new {
  background: linear-gradient(135deg, #66ff66 0%, #00cc00 100%);
  color: #ffffff;
  border-color: #008800;
  animation: glow-badge 2s ease-in-out infinite;
}

@keyframes pulse-badge {
  0%,
  100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
}

@keyframes glow-badge {
  0%,
  100% {
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
  }
  50% {
    box-shadow: 0 0 8px rgba(0, 255, 0, 0.6);
  }
}

/* Responsive adjustments */
@media screen and (max-width: 768px) {
  .project-image {
    width: 100%;
    height: 300px;
  }

  .projects {
    min-height: 350px;
  }

  .project {
    min-height: 350px;
  }
}

@media screen and (max-width: 540px) {
  .projects {
    min-height: 300px;
    margin: 20px 0;
  }

  .project {
    min-height: 300px;
  }

  .project-image {
    height: 250px;
    padding: 4px;
    border-width: 3px;
  }

  .portfolio-nav {
    font-size: 24px;
  }

  .portfolio-nav.left {
    left: 15px;
  }

  .portfolio-nav.right {
    right: 15px;
  }

  .project-info h2 {
    font-size: 14px;
  }

  .project-counter {
    font-size: 9px;
    padding: 6px;
  }
}
